import React, { Component } from "react";
import kevinPoster from "../assets/images/kevin-poster.png";

class Navbar extends Component {
  // eslint-disable-next-line
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div id="nav-div">
        <nav id="navbar" className="animated slideInDown">
          <div id="nav-home-div">
            <img src={kevinPoster} alt="" onClick={this.props.switchWelcome} value="welcome" />
          </div>
          <div id="nav-links-div">
            <ul id="nav-links">
              <li
                id="about-nav-link"
                className={
                  this.props.currentPage === "about"
                    ? "nav-link active margin-both"
                    : "nav-link margin-both"
                }
                onClick={this.props.switchAbout}
                value="about">
                About
              </li>
              <li
                id="portfolio-nav-link"
                className={
                  this.props.currentPage === "portfolio"
                    ? "nav-link active margin-both"
                    : "nav-link margin-both"
                }
                onClick={this.props.switchPortfolio}
                value="portfolio">
                Portfolio
              </li>
              <li
                id="skills-nav-link"
                className={
                  this.props.currentPage === "skills"
                    ? "nav-link active margin-both"
                    : "nav-link margin-both"
                }
                onClick={this.props.switchSkills}
                value="skills">
                Skills
              </li>
              <li
                id="contact-nav-link"
                className={
                  this.props.currentPage === "contact"
                    ? "nav-link active margin-left"
                    : "nav-link margin-left"
                }
                onClick={this.props.switchContact}
                value="contact">
                Contact
              </li>
            </ul>
          </div>
        </nav>
      </div>
    );
  }
} // end of About Component

export default Navbar;
