import React, { Component } from "react";
import Welcome from "./components/Welcome";
import About from "./components/About";
import Navbar from "./components/Navbar";
import Portfolio from "./components/Portfolio";
import Skills from "./components/Skills";
import Contact from "./components/Contact";

import { library } from "@fortawesome/fontawesome-svg-core";
// eslint-disable-next-line
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { fab } from "@fortawesome/free-brands-svg-icons";
import {
  faEnvelope,
  faArrowRight,
  faArrowLeft,
  faTimes,
  faExternalLinkAlt,
  faExternalLinkSquareAlt,
  faWindowClose,
  faCircle,
  faCode,
  faUser,
  faLaptop,
  faDesktop,
  faTabletAlt,
  faMobileAlt,
  faServer,
  faDatabase
} from "@fortawesome/free-solid-svg-icons";
library.add(
  fab,
  faEnvelope,
  faArrowRight,
  faArrowLeft,
  faTimes,
  faExternalLinkAlt,
  faExternalLinkSquareAlt,
  faWindowClose,
  faCircle,
  faCode,
  faUser,
  faLaptop,
  faDesktop,
  faTabletAlt,
  faMobileAlt,
  faServer,
  faDatabase
);

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      navbarShow: false,
      currentPage: "welcome"
    };
    this.switchAbout = this.switchAbout.bind(this);
    this.switchPortfolio = this.switchPortfolio.bind(this);
    this.switchSkills = this.switchSkills.bind(this);
    this.switchContact = this.switchContact.bind(this);
    this.switchWelcome = this.switchWelcome.bind(this);
  } // end of constructor

  switchAbout(tag) {
    this.setState({ currentPage: "about" });
    document.body.classList.remove("welcome-body");
    if (tag !== undefined) {
      this.setState({ navbarShow: true });
    }
  }

  switchPortfolio() {
    this.setState({ currentPage: "portfolio" });
  }

  switchSkills() {
    this.setState({ currentPage: "skills" });
  }

  switchContact() {
    this.setState({ currentPage: "contact" });
  }

  switchWelcome() {
    this.setState({ currentPage: "welcome", navbarShow: false });
    document.body.classList.add("welcome-body");
  }

  render() {
    return (
      <div id="app-container">
        {this.state.navbarShow && (
          <Navbar
            switchAbout={this.switchAbout}
            switchPortfolio={this.switchPortfolio}
            switchSkills={this.switchSkills}
            switchContact={this.switchContact}
            switchWelcome={this.switchWelcome}
            currentPage={this.state.currentPage}
          />
        )}
        {this.state.currentPage === "welcome" && <Welcome switchAbout={this.switchAbout} />}
        {this.state.currentPage === "about" && <About />}
        {this.state.currentPage === "portfolio" && <Portfolio />}
        {this.state.currentPage === "skills" && <Skills />}
        {this.state.currentPage === "contact" && <Contact />}
      </div>
    );
  }
} // end of App component

export default App;
